.aboutcon {
    background-image: url('../media/f.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    width: 93%;
    height: 800px;
    margin-top: 100px;
    display: flex;
    align-items: center;
    padding: 12px 50px;
    display: flex;
    justify-content: space-around;
}
@keyframes doodle {
   
        0% {
          transform: rotate(-4deg);
        }
        50% {
          transform: rotate(4deg);
        }
        100% {
          transform: rotate(-4deg);
        }
  }


  
  
/* Apply the animations to the images */
.me2 {
    width: 30%;
    height: 700px;
    border-radius: 90% / 90% 90%;
    animation: doodle 8s infinite;
    position: relative;

   
}

.aboutme {
width: 40%;
    border: 2px solid red;
    float: right;
}